@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
html {
  font-size: 10px; }

body {
  background: #f7f7f7; }

* {
  font-family: "Open Sans";
  box-sizing: border-box; }

.btn {
  text-transform: capitalize;
  text-align: center;
  font-size: 1.6rem;
  background: transparent;
  border: none;
  outline: none;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  cursor: pointer;
  font-weight: 600; }

.grid {
  margin: 0 auto;
  padding: 4rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, 250px);
  grid-gap: 2rem;
  gap: 2rem;
  justify-content: center; }

.ratio-container {
  width: 100%;
  background-color: #eee;
  position: relative; }
  .ratio-container * {
    position: absolute;
    max-width: 100%;
    max-height: 100%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }

.a24by9 {
  padding-bottom: calc(9 / 24 * 100%); }

.loader {
  border: 4px solid #eee;
  border-radius: 50%;
  border-top: 4px solid #3366ff;
  width: 40px;
  height: 40px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.course-card {
  background: #fff;
  border-radius: 0.5rem;
  overflow: hidden;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.2);
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  outline: none;
  display: flex;
  flex-direction: column; }
  .course-card:hover, .course-card:focus-within {
    box-shadow: 0px 16px 40px 1px rgba(0, 0, 0, 0.08);
    -webkit-transform: scale(1.02);
            transform: scale(1.02); }
  .course-card .card-body {
    flex: 1 1; }
    .course-card .card-body .content {
      padding: 1.5rem; }
      .course-card .card-body .content__title {
        font-size: 1.8rem;
        margin-bottom: 0.5rem;
        text-transform: capitalize; }
      .course-card .card-body .content__description {
        font-size: 1.4rem;
        color: #666; }
    .course-card .card-body .course-progress {
      color: #999; }
      .course-card .card-body .course-progress .status {
        text-transform: capitalize;
        padding: 1.5rem 1.5rem 0.5rem 1.5rem;
        font-size: 1.4rem; }
      .course-card .card-body .course-progress .progress {
        height: 2px;
        background: #eee;
        width: 100%;
        position: relative; }
        .course-card .card-body .course-progress .progress__bar {
          height: 100%;
          background: #21936a; }
  .course-card .card-footer .btn {
    width: 100%;
    padding: 1rem; }
    .course-card .card-footer .btn:hover, .course-card .card-footer .btn:focus {
      background: rgba(51, 102, 255, 0.2);
      color: #3366ff; }

*::-moz-focus-inner {
  border: 0; }

